import data from '../json/compliance.json';
import {
  getLocaleFromCountry,
  getLocaleFromLanguage,
  getLocaleMessage,
  getMappedLocale
} from '../app/components/localization/localized-messages';
import {getSaveCardMandateText} from './mandate';

const defaultDRStore = 'https://store.digitalriver.com/store/defaults/';
const eCommerceProvider = '/eCommerceProvider.';
const slash = '/';
const commerceSupportedLocales = data.locale;

export function complianceGetDetails(entityInfo, locale, country, restrictByCountry) {
  const isEntityInfoAnObject = typeof entityInfo === 'object';
  const fullLocale = typeof entityInfo.language !== 'undefined' ? getLocaleFromLanguage(entityInfo.language) : locale.length > 2 ? locale : getLocaleFromLanguage(locale);
  let updatedLocale = fullLocale.replace('-', '_');
  // todo we need to maybe do this es_419 stuff differently
  updatedLocale = updatedLocale === 'es_419' ? 'es_MX' : updatedLocale;
  let businessEntityNameEncoded;
  let businessEntityName = isEntityInfoAnObject ? getBusinessEntityNameFromCode(entityInfo.businessEntityCode) : getBusinessEntityNameFromCode(entityInfo);

  if(typeof businessEntityName !== 'undefined') {
    businessEntityNameEncoded = encodeURIComponent(businessEntityName);
  }

  if (!checkLocaleSupports(updatedLocale)) {
    throw new Error('Locale is not supported');
  }
  const linkLocale = typeof country === 'undefined' || isEntityInfoAnObject ? updatedLocale : getLocaleFromCountry(country).replace('-', '_');
  const showCancellationRightsCountries = ['de', 'dk', 'es', 'fi', 'fr', 'gb', 'ie', 'it', 'nl', 'no', 'pt', 'se'];
  let showCancellationRights = false;

  if((typeof country !== 'undefined' && showCancellationRightsCountries.indexOf(country.toLowerCase()) !== -1) || !restrictByCountry) {
    showCancellationRights = true;
  }

  let complianceData = {
    'disclosure': {
      'termsOfSale': {
        'localizedText': localizedText(data.keys.TERMS_OF_SALE, updatedLocale, businessEntityName, undefined, undefined, linkLocale),
        'url': localizedUrl(linkLocale, 'DisplayDRTermsAndConditionsPage', businessEntityNameEncoded)
      },
      'privacyPolicy': {
        'localizedText': localizedText(data.keys.PRIVACY_POLICY, updatedLocale, businessEntityName, undefined, undefined, linkLocale),
        'url': localizedUrl(linkLocale, 'DisplayDRPrivacyPolicyPage', businessEntityNameEncoded)
      },
      'cookiePolicy': {
        'localizedText': localizedText(data.keys.COOKIE_POLICY, updatedLocale, businessEntityName, undefined, undefined, linkLocale),
        'url': localizedUrl(linkLocale, 'DisplayDRCookiesPolicyPage', businessEntityNameEncoded)
      },
      'legalNotice': {
        'localizedText': localizedText(data.keys.LEGAL_NOTICE, updatedLocale, businessEntityName, undefined, undefined, linkLocale),
        'url': localizedUrl(linkLocale, 'DisplayDRContactInformationPage', businessEntityNameEncoded)
      },
      'autorenewalPlanTerms': {
        'localizedText': localizedText(data.keys.AUTORENEWAL_PLAN_TERMS, updatedLocale, businessEntityName, undefined, undefined, linkLocale)
      },
      'saveCardMandate': {
        'localizedText': getSaveCardMandateText(updatedLocale, businessEntityName)
      },
      'idealRecurringAgreement': {
        'localizedText': localizedText(data.keys.IDEAL_RECURRING_AGREEMENT, updatedLocale, businessEntityName, undefined, undefined, linkLocale)
      }
    }
  };

  if(typeof businessEntityName !== 'undefined') {
    complianceData.disclosure.businessEntity = {
      'name': businessEntityName,
      'id': isEntityInfoAnObject ? entityInfo.businessEntityCode : entityInfo
    };
    complianceData.disclosure.resellerDisclosure = {
      'localizedText': localizedText(data.keys.RESELLER_DISCLOSURE, updatedLocale, businessEntityName, undefined, undefined, linkLocale),
      'url': localizedUrl(linkLocale, 'DisplayDRAboutDigitalRiverPage', businessEntityNameEncoded)
    };
    complianceData.disclosure.confirmDisclosure = {
      'localizedText': stripOutDoublePeriods(localizedText(data.keys.CONFIRM_DISCLOSURE, updatedLocale, businessEntityName, undefined, undefined, linkLocale))
    };
  }

  if(!isEntityInfoAnObject || (isEntityInfoAnObject && entityInfo.country === 'us')) {
    let californiaPrivacyRights;
    if(!isEntityInfoAnObject) {
      californiaPrivacyRights = getCaliforniaPrivacyRights(updatedLocale, businessEntityName, linkLocale);
    }
    else if(locale === 'en' || locale === 'en-US' || updatedLocale === 'en_IE') {
      californiaPrivacyRights = getCaliforniaPrivacyRights('en_US', businessEntityName, 'en_US');
    }

    if(typeof californiaPrivacyRights !== 'undefined') {
      complianceData.disclosure.californiaPrivacyRights = californiaPrivacyRights;
    }
  }

  if(!isEntityInfoAnObject || (isEntityInfoAnObject && entityInfo.country === 'it')) {
    const warrantyInformation = getWarrantyInformation(updatedLocale, businessEntityName, linkLocale);
    if(typeof warrantyInformation !== 'undefined') {
      complianceData.disclosure.warrantyInformation = warrantyInformation;
    }
  }

  if(showCancellationRights) {
    const cancellationRights = getCancellationRights(updatedLocale, businessEntityName, linkLocale, businessEntityNameEncoded);
    if(typeof cancellationRights !== 'undefined') {
      complianceData.disclosure.cancellationRights = cancellationRights;
    }
  }

  return complianceData;
}

function checkLocaleSupports(locale) {
  return commerceSupportedLocales.includes(locale);
}

export function localizedText(type, locale, businessEntityName, payNowButtonText, createAccountButtonTextClass, linkLocale) {

  const chosenDefaultLinkLocale = typeof linkLocale === 'undefined' ? locale : linkLocale;

  let localizedValue;
  if( (locale in data.details) && (type in data.details[locale])) {
    localizedValue = data.details[locale][type];
  } else {
    localizedValue = '';
  }
  localizedValue = evaluateVars(localizedValue, locale, businessEntityName, payNowButtonText, createAccountButtonTextClass, chosenDefaultLinkLocale);
  return localizedValue;
}

function evaluateVars(string, locale, businessEntityName, payButtonText, createAccountButtonTextClass, linkLocale) {

  const replacementRules = {
    termsOfSaleUrl: localizedUrl(linkLocale, 'DisplayDRTermsAndConditionsPage', encodeURIComponent(businessEntityName)),
    privacyPolicyUrl: localizedUrl(linkLocale, 'DisplayDRPrivacyPolicyPage', encodeURIComponent(businessEntityName)),
    resellerDisclosureUrl: localizedUrl(linkLocale,'DisplayDRAboutDigitalRiverPage', encodeURIComponent(businessEntityName)),
    businessEntityName: businessEntityName,
    payNow: typeof payButtonText !== 'undefined' ? payButtonText : getLocaleMessage(locale.split('_').join('-'), 'payNow'), //TODO refactor so localized messages and compliance data use same format for locale
    createAccountButtonTextClass: createAccountButtonTextClass
  };
  const replacementsPattern = /[^{}]+(?=})/g;
  const varsPattern =  /{([^}]+)}/gm;
  const replacements = string.match(replacementsPattern);
  const varsToReplace = string.match(varsPattern);


  if(varsToReplace !== null) {
    varsToReplace.forEach((stringVar, index) => {
      string = string.replace(stringVar, replacementRules[replacements[index]]);
    });
  }
  return(string);
}

function localizedUrl(locale, entityType, entityNameEncoded, anchor) {
  let url;
  if(typeof entityNameEncoded === 'undefined') {
    url = defaultDRStore + locale + slash + entityType;
  }
  else {
    url = defaultDRStore + locale + slash + entityType + eCommerceProvider + entityNameEncoded;
  }
  if(anchor) {
    url = `${url}#${anchor}`;
  }
  return url;
}


function getBusinessEntityNameFromCode(entityId) {
  let entityName;
  data.entityCode.forEach( function (entity) {
    if(typeof entityId === 'string' && entityId.toUpperCase() === entity.code) {
      entityName = entity.name;
    }
  });
  return entityName;
}


function getCaliforniaPrivacyRights(updatedLocale, businessEntityName, linkLocale) {
  let californiaPrivacyRights;
  const californiaPrivacyRightText = localizedText(data.keys.CALIFORNIA_PRIVACY_RIGHTS, updatedLocale, businessEntityName);

  if(californiaPrivacyRightText !== '' && existsForLocale(data.keys.CALIFORNIA_PRIVACY_RIGHTS, linkLocale, businessEntityName)) {
    californiaPrivacyRights = {
      'localizedText': californiaPrivacyRightText,
      'url': 'https://store.digitalriver.com/store/defaults/en_US/DisplayCCPAPage'
    };
  }

  return californiaPrivacyRights;
}

function getCancellationRights(updatedLocale, businessEntityName, linkLocale, businessEntityNameEncoded) {
  let cancellationRights;
  const cancellationRightsText = localizedText(data.keys.CANCELLATION_RIGHTS, updatedLocale, businessEntityName, undefined, undefined, linkLocale);

  if(cancellationRightsText !== '' && existsForLocale(data.keys.CANCELLATION_RIGHTS, linkLocale, businessEntityName)) {
    cancellationRights = {
      'localizedText': cancellationRightsText,
      'url': localizedUrl(linkLocale, 'DisplayDRTermsAndConditionsPage', businessEntityNameEncoded, 'cancellationRight')
    };
  }

  return cancellationRights;
}

function existsForLocale(key, locale, businessEntityName) {
  return localizedText(key, locale, businessEntityName).length > 0;
}

function getWarrantyInformation(updatedLocale, businessEntityName, linkLocale) {
  let warrantyInformation;
  const warrantyInformationText = localizedText(data.keys.WARRANTY_INFORMATION, updatedLocale, businessEntityName);
  const warrantyInformationUrl = localizedUrl(linkLocale, 'DisplayDRTermsAndConditionsPage', typeof businessEntityName !== 'undefined' ? encodeURIComponent(businessEntityName) : businessEntityName, 'warrantyInformation');
  if(warrantyInformationText !== '' && existsForLocale(data.keys.WARRANTY_INFORMATION, linkLocale, businessEntityName)) {
    warrantyInformation = {
      'localizedText': warrantyInformationText,
      'url': warrantyInformationUrl
    };
  }

  return warrantyInformation;
}

export function getAutoRenewPlanTerms(locale, businessEntityId) {
  const updatedLocale = locale.replace('-','_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return localizedText(data.keys.AUTORENEWAL_PLAN_TERMS, updatedLocale, businessEntityName);
}

export function stripOutDoublePeriods(text) {
  return text.split('..').join('.');
}

export function getConfirmDisclosure(locale, businessEntityId) {
  const updatedLocale = locale.replace('-','_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return stripOutDoublePeriods(localizedText(data.keys.CONFIRM_DISCLOSURE, updatedLocale, businessEntityName));
}

export function getAgreeToTerms(locale, businessEntityId) {
  const updatedLocale = locale.replace('-','_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return localizedText(data.keys.AGREE_TO_TERMS, updatedLocale, businessEntityName);
}

export function getActiveAcceptance(locale, businessEntityId, paymentMethodType, buttonText, customClasses) {
  const exactLocaleMatchExists = data.details[locale.replace('-','_')].hasOwnProperty(paymentMethodType + 'ActiveAcceptance');
  const updatedLocale = exactLocaleMatchExists ? locale.replace('-','_') : getMappedLocale(locale).replace('-', '_');
  const businessEntityName = getBusinessEntityNameFromCode(businessEntityId);
  return localizedText(`${paymentMethodType}ActiveAcceptance`, updatedLocale, businessEntityName, buttonText, customClasses);
}